    /** This function checks that the server responded with a success code. If it did,
        the promise was kept and the program uses the received data. If a failure, it
        lets the calling function know the error code */
    function checkStatus(response) {
        if (response.status >= 200 && response.status < 300) {
            //Successful request; return the data
            return response.text();
	} else if(response.status === 404) {
            //Data requested wasn't found
            return Promise.reject(new Error(response.status));
        } else {
            return Promise.reject(new Error(response.status+":"+response.statusText));
        }
    }

    function getProfileOf(name, callback) {
	let url = siteUrl + "?mode=profile&name=" + name;
	fetch(url)
	.then(checkStatus)
        .then(function(responseText) {
	    let profile = JSON.parse(responseText)[0];
	    // profile = { "name": "some name", "bio": "some bio text here"}
	    // Do some stuff with the profile dictionary
	    callback(profile);
	})
         .catch(function(error) {
	     console.log(error);
	 });
    }

    function getReviewsOf(name, callback) {
	let url = siteUrl + "?mode=reviews&name=" + name;
	fetch(url)
	    .then(checkStatus)
	    .then(function(responseText) {
		let reviews = JSON.parse(responseText);
		// reviews = [{"rating" : 5, "text" : "some text here", "name" : "Cole"},
		//            {"rating" : 4, "text" : "some text here", "name" : "Matt"},
		//            ....]
		callback(reviews);
	    })
	    .catch(function(error) {
		console.log(error);
	});
    }

    function searchPosts(parameters, callback) {
	let url = siteUrl + "?mode=search";
	let filters = [];
	if(parameters["sale_type"]) {
	    filters.push("sale_type=" + parameters["sale_type"]);
	}
	if(parameters["item_name"]) {
	    filters.push("item_name=" + parameters["item_name"]);
	}
	if(parameters["curr_status"]) {
	    filters.push("curr_status=" + parameters["curr_status"]);
	}
	if(parameters["community"]) {
	    filters.push("community=" + parameters["community"]);
	}
	if(filters.length > 0)
	    url += "&" + filters.join("&");
	console.log(url);
	fetch(url)
	    .then(checkStatus)
	    .then(function(responseText) {
		let posts = JSON.parse(responseText);
		callback(posts);
	    })
	    .catch(function(error) {
		console.log(error);
	});
    }

    function postItem(item_name, seller, community, price, condition, description,
		      curr_status, sale_type) {
	const message = { "mode": "post-item",
			  "seller": seller,
			  "item_name": item_name, "community": community,
			  "price": price, "condition": condition,
			  "description": description, "curr_status": curr_status,
			  "sale_type": sale_type };
	const fetchOptions = {
                method : 'POST',
                headers : {
                    'Accept' : 'application/json',
                    'Content-Type' : 'application/json'
                },
                body : JSON.stringify(message)
            };
        let url = siteUrl;
	fetch(url, fetchOptions)
            .then(checkStatus)
            .then(function(responseText) {
		console.log(responseText);
            })
            .catch(function(error) {
		console.log(error);
            });
    }

    function postReview(reviewer, reviewed, rating, text) {
	const message = { "mode": "post-review",
			  "reviewer": reviewer,
			  "reviewed": reviewed,
			  "rating": rating,
			  "text": text };
	const fetchOptions = {
                method : 'POST',
                headers : {
                    'Accept' : 'application/json',
                    'Content-Type' : 'application/json'
                },
                body : JSON.stringify(message)
            };
        let url = siteUrl;
	fetch(url, fetchOptions)
            .then(checkStatus)
            .then(function(responseText) {
		console.log(responseText);
            })
            .catch(function(error) {
		console.log(error);
            });
    }

    function createUser(name, bio) {
	const message = { "mode": "create-user",
			  "name": name,
			  "bio": bio };
	const fetchOptions = {
            method : 'POST',
            headers : {
                'Accept' : 'application/json',
                'Content-Type' : 'application/json'
            },
            body : JSON.stringify(message)
        };
        let url = siteUrl;
	fetch(url, fetchOptions)
            .then(checkStatus)
            .then(function(responseText) {
		console.log(responseText);
            })
            .catch(function(error) {
		console.log(error);
            });
    }

